				"p",
				null,
				"Projects start by proposing ideas and sanity checking them. Some news stories you may have read about what can be done with data science are accurate, but many are hyperbolic and don\u2019t help you think clearly about what\u2019s possible. Involving a data scientist from the start will let you test the feasibility of ideas and give a sense of the costs, time and chance of success. They can also suggest different ideas or approaches to a business challenge you might not have considered."
